import React, { useState, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { PrimeReactProvider, PrimeReactContext } from 'primereact/api';








import "primereact/resources/themes/lara-light-cyan/theme.css";
import 'primeicons/primeicons.css';
import "primeflex/primeflex.css";
import "./../App.css"








import { Button } from 'primereact/button';








export default function NoEffortMessages() {








    const navigate = useNavigate();








    return (
        <div className="flex flex-column gap-5 text-center">
            {/* <div className="font-starline text-7xl py-3 text-center">no-effort message</div> */}
            <div className="flex flex-column gap-3">
                <div className="font-urbanist font-bold text-5xl">Wrote me a no-effort message?</div>
                <div className="font-urbanist text-3xl">Sorry, but I'm not interested in chatting with you at the moment.</div>
                <div className="font-urbanist text-xl"><i>Keep reading to find out why, and how you can redeem yourself!</i></div>
            </div>
            <div className="flex flex-column gap-3 text-left">
                {/* <div>While I love to chat, play, share gear, and have fun, I only do those things with people who </div>
                <div>WhiI love to chat, play, share gear, and have fun! However, I only do those things to the people who put in a little effort  </div> */}
                <div><i>I still can't believe that I have to write this, but my inbox is getting out-of-control with the amount of no-effort messages I get.</i></div>
                <div>I love to chat, answer questions, play, share gear, and have some kinky fun! However, I only do those things for the people can show me respect and that they care; it starts by putting in a little effort.</div>
                <div>When I converse with someone, I want to be certain that they have a genuine interest in me, and show respect for my limited time. It's very hard to prove that when the message is simply "hey", "how r u", "ur hot", etc...</div>
                <div>My experiences have taught me that 9 times out of 10, it's not worth it to respond to no-effort messages such as yours. In general, the messenger cannot hold a conversation, always talks in slang, is looking for instant gratification, and cares little about who I am.</div>
                <div>My goal is to make friends and connections, and that starts by finding quality people. My time is limited, and I can't talk with everyone, so I choose to converse with those people who put in that effort.</div>
                <div>It's extremely frustrating when most people skip over my detailed profile, write a no-effort message, yet still expect to talk/play with me. I want to have a good time; but it's hard to do that when I have to tell grown adults how to socialize and properly converse with people. It's basic courtesy to read a person's profile and write them an appropriate introductory message.</div>
                <div>Respect goes both ways. Please, show me some respect, that you care about me, and you will get the same back.</div>
                <div>
                    With all this said, I do offer a path to redemption. If you still want to talk with me, here's my suggested 3-step plan:
                </div>
            </div>
            <div className="flex flex-column lg:flex-row gap-5 justify-content-center align-items-start">
                <div className="flex flex-column justify-content-center text-center gap-3">
                    <div className="mx-auto border-circle border-2 border-900 border-solid w-3rem h-3rem flex flex-row align-items-center justify-content-center font-urbanist text-3xl font-bold">1</div>
                    <div>
                        Go through my about page, read up on who I am, and understand my kinks, interests, and what I'm looking for.
                    </div>
                    <Button className="button align-self-center" label="about" onClick={() => navigate("/about")} severity="info" />
                </div>
                <div className="flex flex-column justify-content-center text-center gap-3">
                    <div className="mx-auto border-circle border-2 border-900 border-solid w-3rem h-3rem flex flex-row align-items-center justify-content-center font-urbanist text-3xl font-bold">2</div>
                    <div>
                        If you are looking to play, take some time and fill out the vetting form, so I can understand your kinks and needs. This helps me determine if we're a good fit together.
                    </div>
                    <Button className="button align-self-center" label="vetting form" onClick={() => navigate("/vetting-form")} severity="info" />
                </div>
                <div className="flex flex-column justify-content-center text-center gap-3">
                    <div className="mx-auto border-circle border-2 border-900 border-solid w-3rem h-3rem flex flex-row align-items-center justify-content-center font-urbanist text-3xl font-bold">3</div>
                    <div>
                        Send me another message, but this time please put some effort in. Write 2-3 full sentences introducing yourself, and tell me why you're reaching out.
                    </div>
                    <div>Make sure to include the word "trapezoid" in your new message so I know that I got through to you!</div>
                </div>
            </div>
            <div className="flex flex-column text-left gap-2">
                <div className="font-urbanist font-bold text-2xl">What's the takeaway from all this?</div>
                <div>Even if you do decide to not message me again, understand that for myself, and a lot of the kink community, no-effort messages are extremely annoying to deal with. As much as I'd like to talk to more people, 9 times out of 10 I end up wasting my time by responding to these no-effort messages, with those conversations going nowhere.</div>
                <div>I have been fortunate to have some amazing experiences with some hard-to-access people simply because I am respectful of people's time, talk in complete sentences, and am able to hold a conversation. And going forward, I suggest you do the same.</div>
            </div>
            <div>Thank you for taking the time to read this. I hope to have a great conversation with you in the future!</div>
        </div>
    )
};